.statistics {
  box-sizing: border-box;
  background-color: rgb(207, 177, 187);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  width: 500px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  margin-bottom: 20px;
}

.title {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: #000;
  font-size: 20px;
  font-weight: 600;
}

.statsList {
  display: flex;
  margin: 0;
  align-items: center;
  list-style: none;
  justify-content: center;
  padding: 20px;
  width: 100%;
  background-color: rgb(236, 240, 245);
}

.item {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.item:nth-child(odd) {
  background: #e1f1ff;
}
.item:nth-child(even) {
  background: white;
}

.label {
  color: rgb(51, 50, 50);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 10px;
}

.percentage {
  color: rgb(90, 81, 81);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 17px;
  font-weight: 600;
}